












































































import { Component, Vue } from 'vue-property-decorator';
import BaseSloganCard from '@/components/base/BaseSloganCard.vue';

@Component({
  components: {
    BaseSloganCard,
  },
})
export default class ServicesPage extends Vue {
  activePage = this.$route.name;

  readonly serviceItems = [
    {
      id: 0,
      value: 'services-paint-and-drywall',
      text: 'Painting and drywalling',
    },
    {
      id: 1,
      value: 'services-door-and-window',
      text: 'Door and window repairs',
    },
    {
      id: 2,
      value: 'services-handyman',
      text: 'Handyman service',
    },
  ];

  viewService(routeName?: string): void {
    this.$vuetify.goTo('.contents-area');

    if (routeName) {
      this.$router.push({ name: routeName });
    }
  }
}
